
.text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.v-leftbar {
  width: 17%;
  border-right: 1px solid #cfc7c1;
}
.v-leftbar .v-left-ul-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.v-leftbar .v-left-ul-wrapper .v-leftbar-ul {
  font-size: 16px;
  margin: 8px 0;
  cursor: default;
}
.v-leftbar .v-left-ul-wrapper .v-leftbar-ul .tle {
  height: 48px;
  line-height: 48px;
  font-size: 14px;
  padding-left: 28px;
  color: #4a4a4a;
}


