
.text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.v-logo {
  cursor: pointer;
  width: 80%;
  margin: 20px 20px 40px;
  padding: 10px;
  display: block;
  position: relative;
  top: 20px;
  border: 1px solid transparent;
  border-radius: 12px;
  transition: all 1s;
}
.v-logo:hover {
  border: 1px solid #fdba78;
}
.v-logo .logo {
  border: none;
  width: 40px;
  height: 40px;
  float: left;
  box-sizing: border-box;
  padding: 1% 5px 0 0;
  margin-right: 10px;
}
.v-logo .logo .tle {
  font-size: 16px;
}
.v-logo .logo .tle:active {
  text-decoration: none;
}
.v-logo .logo .slogan {
  font-size: 12px;
}


