
.text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.v-displaybox {
  position: relative;
  width: 100%;
  border: 1px solid #eee;
  border-radius: 6px;
  margin-bottom: 20px;
  overflow: hidden;
}
.v-displaybox:after {
  clear: both;
  content: '';
  display: block;
}
.v-displaybox:hover {
  box-shadow: 0 2px 7px rgba(0,0,0,0.15);
  border-color: transparent;
}
.v-displaybox .left {
  float: left;
  width: 50%;
  border-right: 1px dotted #eee;
  overflow: hidden;
}
.v-displaybox .left .display {
  width: 100%;
  padding: 10px 10px 15px;
}
.v-displaybox .left .header {
  position: relative;
}
.v-displaybox .left .header:before {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: #eee;
  position: absolute;
  top: 10px;
  left: 0;
}
.v-displaybox .left .header span {
  display: inline-block;
  background: #fff;
  padding: 0 5px 0 12px;
  position: relative;
  margin-left: 30px;
  font-size: 14px;
}
.v-displaybox .left .desc {
  padding: 10px 10px 15px;
}
.v-displaybox .right {
  width: 49%;
  float: right;
  padding: 10px 5px;
  height: 0;
  transition: height 0.2s ease-in-out;
}
.v-displaybox .right section {
  width: 100%;
  overflow-x: scroll;
}
.v-displaybox .right .more {
  position: absolute;
  bottom: 0;
  margin-left: -15px;
  width: 50%;
  box-shadow: inset 0 -15px 30px #fff;
  text-align: center;
  cursor: pointer;
}
.v-displaybox .right .more .icon {
  line-height: 1;
  font-size: 5px;
  font-weight: 100;
  color: #ccc;
}
.v-displaybox .right .more:hover .icon {
  color: #ed6c00 !important;
}


