
.v-contentzone {
  padding: 2% 2% 2% 3%;
  width: 82%;
}
.v-contentzone h1 {
  margin: 2% 0;
  font-size: 24px;
  font-weight: 400;
}
.v-contentzone h2 {
  margin: 1.5% 0;
  font-weight: 400;
}
.v-contentzone h3 {
  margin: 1% 0;
  font-weight: 400;
}
.v-contentzone p {
  margin: 0.5% 0;
  font-size: 14px;
}
.v-contentzone li {
  margin: 1% 3%;
}
.v-contentzone table {
  font-family: Consolas, Menlo, Courier, monospace;
  font-size: 12px;
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
  border: 1px solid #e9e9e9;
  width: 100%;
  margin-bottom: 24px;
}
.v-contentzone table thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
}
.v-contentzone table th,
.v-contentzone table td {
  border: 1px solid #e9e9e9;
  padding: 8px 16px;
  text-align: left !important;
}
.v-contentzone table th {
  background: #f7f7f7;
  white-space: nowrap;
  color: #5c6b77;
  font-weight: 600;
}
.v-contentzone table tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.v-contentzone table tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}
.v-contentzone pre code {
  background: #fff;
  border: none;
}
.v-contentzone pre code .hljs-tag {
  color: #3e76f6;
}
.v-contentzone pre code .hljs-attr,
.v-contentzone pre code .hljs-keyword {
  color: #e96900;
}
.v-contentzone pre code .hljs-string,
.v-contentzone pre code .hljs-value {
  color: #42b983;
}
.v-contentzone code {
  display: inline-block;
  background: #f7f7f7;
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  margin: 0 3px;
  padding: 1px 5px;
  border-radius: 3px;
  color: #666;
  border: 1px solid #eee;
}


